  methods: {
    search: function () {
      this.$http.post('/data/analyze/', {account: "0xi8l4rv7k4y9buf72i0lh6yw1y12aq8mwduanw940", jump: 3}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function (res) {
                    // this.token = res.body.token
                    this.code = 200
                    // this.activeNumber = 3
                    this.option4Analyze = {
                      "nodes": res.body.nodes,
                      "edges": res.body.edges,
                    }
                }, function (err) {
                    this.code = err.status
                    this.message = err.body.message
                })
      this.$http.post('/data/overview/', {account: "0xi8l4rv7k4y9buf72i0lh6yw1y12aq8mwduanw940", jump: 3}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function (res) {
